
.loader[data-v-50aac28c] {
  position: fixed;
  z-index: 999999999;
  width: 100%;
  height: 100%;
  background:#1f2025;
  text-align: center;
  /* loader页面消失采用渐隐的方式*/
  -webkit-transition: opacity 1s ease;
  transition: opacity 1s ease;
}
/* 使用base64编码嵌入加载的logo */
.loader-content[data-v-50aac28c] {
  display: block;
  position: fixed;
  width: 100%;
  height: 80px;
  top: 0;
  bottom:0;
  left: 0;
  right: 0;
  margin: auto;
}
.fadeout[data-v-50aac28c] {
  opacity: 0;
  filter: alpha(opacity=0);
}
/* logo出现动画 */
@-webkit-keyframes fadeInDown-data-v-50aac28c{
0%{opacity:0;-webkit-transform:translate3d(0,-100%,0);transform:translate3d(0,-100%,0)
}
100%{opacity:1;-webkit-transform:none;transform:none
}
}
@keyframes fadeInDown-data-v-50aac28c{
0%{opacity:0;-webkit-transform:translate3d(0,-100%,0);
}
}



